import { Button } from "react-bootstrap"
import React, { useEffect, useState } from 'react';
import axios from "axios";

const LoginForm = () => {
    var [modalBg, setModalBg] = useState();
    const modalClose = document.querySelector(".btn-close");
    // const url = "http://18.117.255.214:8082/";
    const url2 = "http://localhost:8082/";
    const url3 = "http://localhost:8082/hello";
    const url4 = "http://localhost:8082/api/auth/signin";
    const url5 = "http://18.216.242.4:8082/api/auth/signin";
    const [data, setData] = useState({
        username: "",
        password: ""
    });

    useEffect(() => {
        setModalBg(document.getElementById("modal"));
        return () => {

        }
    }, [])

    const handleClose = (e) => {
        e.preventDefault();
        modalBg.classList.remove("bg-active");
    }

    function handleData(e) {
        const newData = { ...data }
        newData[e.target.id] = e.target.value;
        setData(newData);
        // console.log(newData);


    }

    async function handleSubmit(e) {
        e.preventDefault();
        console.log("Client is attempting Login");
        try {
            await axios.post(url5, {
                username: data.username,
                password: data.password

            }).then(res => {
                console.log(res);
                modalBg.classList.remove("bg-active");
            }).catch(function (error) {
                console.log("Could not loggin " + error);
            })
        } catch (e) {
            console.log("Could not loggin" + e);
        }
    }

    function handleSignIn(e) {
        e.preventDefault();

    }
    // console.log(data);
    return (
        <div className="">

            <div className="flex justifyend">

                <div className=" ">
                    <button onClick={handleClose}
                        type="button" className="btn-close modal-close" style={{ fontSize: "18px" }} aria-label="Close">X</button>
                </div>

            </div>
            <div className="flex justify">
                <h2 class="form-signin-heading">Please sign in</h2>
            </div>
            <br />

            <hr className="hrgreen" />


            <form class="form-signin flexcol alignitems"
                // method="post" action="/login"
                onSubmit={e => handleSubmit(e)}>

                <p style={{ width: "100%" }} className="justify flex" >
                    <label for="username" class="sr-only">Username</label>
                    <input type="text" id="username" name="username" className="form-control padding3 margintop10"
                        placeholder="Username" required="" autofocus=""
                        style={{ width: "100%" }}
                        onChange={(e) => handleData(e)}
                        value={data.username} />
                </p>
                <p style={{ width: "100%" }} className="justify flex" >
                    <label for="password" class="sr-only">Password</label>
                    <input style={{ width: "100%" }}
                        type="password" id="password" name="password"
                        className="form-control padding3 margintop" placeholder="Password" required
                        onChange={e => handleData(e)} value={data.password}
                    />
                </p>
                <input name="" type="hidden" />
                <br />
                {/* <hr className="hrgreen" /> */}
                <div style={{ width: "100%" }} className="flex justify margintop">
                    <button
                        //  onClick={handleSignIn}
                        className=" modalbutton"
                        style={{}}
                        size="lg" type="submit">Sign in</button>
                </div>

                <div style={{ width: "100%" }} className="flex justifyend margintop10">
                    <button className="modalbutton" onClick={handleClose}
                        style={{ width: "30%", backgroundColor: "#dc3545" }}
                        size="lg" type="submit">Close</button>
                </div>

                <br />
                <hr className="hrgreen" />
                <div class=" text-muted" style={{ textAlign: "center", fontSize: "0.6em" }}>
                    Need to Signup?</div>
                <div style={{ textAlign: "center", fontSize: "0.7em" }}  >
                    <a href="./userregister" class="modalBtn btn-sm btn-link">
                        Register Now</a>
                </div>
            </form>

        </div>
    )
}

export default LoginForm
